body {
  color: #fff;
  background-color: #1c1c1c;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 50px;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.grid {
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
  width: 100%;
  display: flex;
}

.grid__main-responsive-grid {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  grid-template-columns: repeat(2, 100px);
  justify-content: center;
  gap: 40px 30px;
  width: 230px;
  padding: 20px;
  display: grid;
}

@media (width >= 750px) {
  .grid__main-responsive-grid {
    grid-template-columns: repeat(3, 100px);
    width: 360px;
  }
}

@media (width >= 1000px) {
  .grid__main-responsive-grid {
    grid-template-columns: repeat(4, 100px);
    width: 490px;
  }
}

@media (width >= 1500px) {
  .grid__main-responsive-grid {
    grid-template-columns: repeat(5, 100px);
    width: 620px;
  }
}

.grid__item {
  box-sizing: border-box;
  background-color: #2a2a2a;
  border-radius: 50%;
  flex-direction: column;
  align-items: center;
  width: 100px;
  height: 100px;
  padding-top: 0;
  transition: transform 1s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
}

.grid__item:hover {
  z-index: 10;
  background-color: #fff;
  border-radius: 50%;
  transform: scale(2);
}

.grid__item:hover .person__image {
  filter: grayscale(0%);
  border-radius: 50%;
}

.grid__item:hover .person__details {
  opacity: 1;
  color: #000;
  background-color: #ffffffe6;
  transform: translateY(0);
}

.grid__item:hover .person__name, .grid__item:hover .person__sport, .grid__item:hover .person__pay {
  color: #000;
}

.grid__item:hover .person__rank {
  color: #fff;
  background-color: #000000b3;
}

.grid__item--hover {
  transition: transform;
}

.grid__item--hover:hover {
  border-radius: 50%;
}

.grid__item--hover .person__image {
  border-radius: 50%;
  transition: filter, border-radius;
}

.grid__item--hover .person__details {
  transition: opacity, transform;
}

.grid__item--delay {
  transition: transform 1s ease-in-out;
}

.grid__item--delay:hover {
  border-radius: 50%;
}

.grid__item--delay .person__image {
  border-radius: 50%;
  transition: filter 1s ease-in-out, border-radius 1s ease-in-out;
}

.grid__item--delay .person__details {
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
}

.grid__hover-section {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  grid-template-columns: repeat(5, 100px);
  justify-content: center;
  gap: 40px 30px;
  width: 620px;
  margin-top: 50px;
  padding: 20px;
  display: grid;
}

.person {
  text-align: center;
  background-color: inherit;
  flex-direction: column;
  align-items: center;
  width: 100px;
  height: 100px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.person__photo {
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.person__image {
  object-fit: cover;
  filter: grayscale(0%);
  border-radius: 50%;
  width: 100px;
  height: 100px;
  transition: filter 1s ease-in-out, border-radius 1s ease-in-out;
}

.person__rank {
  color: #fff;
  z-index: 2;
  background-color: #000000b3;
  border-bottom-right-radius: 5px;
  padding: 2px 5px;
  font-size: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

.person__details {
  color: #fff;
  opacity: 0;
  box-sizing: border-box;
  background-color: #000000b3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 10px 0;
  font-size: 12px;
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(100%);
}

.person__name {
  color: inherit;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 5px;
  padding: 0 5px;
  font-weight: bold;
  text-decoration: none;
  display: block;
  overflow: hidden;
}

.person__extra {
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 2px;
  font-size: 10px;
  display: flex;
}

.person__pay:after {
  content: "M";
  margin-left: 2px;
}
